.item{
    display: inline-block;
    color: #fff;
    padding: 10px;
    margin-left: 10px;
    cursor: pointer;
    color: #fff;
    position: relative;
    transition: all .3s;
}
.item a{
    color: #fff;
    opacity: 0.5;
    transition: all .3s;
}
.countOfFriends {
    font-size: 10px;
    display: inline-block;
    margin-left: 10px;
    color: #fff;
    padding: 5px;
    transition: all .1s;
}
.item .active{
    opacity: 1;
}
.item .active ::before{
    position: absolute;
    content: '';
    width: 50px;
    height: 2px;
    display: block;
    background-color: #fff;
    bottom: 0;
    left: 10%;

}
.item .active .countOfFriends{
    border: 1px solid #fff;
    border-radius: 5px;
    background-color: #FFF;
    color: darkcyan;
}
.chats{
    padding-left: 30px;
    display: grid;
    grid-template-columns: 3fr 9fr;
}
.windowDialog {
    background-color: rgb(29, 69, 77);
    padding: 20px;
    border-radius: 5px;
}
.textFromMe {
    margin-left: auto;
}
.items {
    margin-bottom: 20px;
}
.frindsList{
    margin-left: 30px;
    width: 500px;
    display: block;
    background-color: rgb(29, 69, 77);
    border-radius: 5px;
    padding: 25px;
}
.friendItem{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #fff;
    border-top: 1px solid #fff;
    padding-top: 10px;
    padding-bottom: 5px;
    margin-bottom: 10px;
}

